import jsPDF from "jspdf";
import "jspdf-autotable";
import { getToday, localStoragedecryp,formatGram, getUserdata } from "../../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanPindahBarang = (
  data = "",
  username = "",
  tanggal_dari = "",
  tanggal_sampai = "",
) => {
  // initialize jsPDF
  const doc = new jsPDF("l", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let footRows = [];
  let finalY = 30;
  let grantotalberat = 0;
  let grantotalberatatr = 0;
  let total_barang = 0;

  doc.setFontSize(15);
  doc.text("LAPORAN PINDAH BARANG", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 200, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0,48), 200, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48,90), 200, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "Laporan Pindah Barang"
  });
  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
          " s/d " +
          JSON.parse(localStoragedecryp("tgl_laporan")).tgl_akhir
    }`,
    14,
    25
  );
  let tableColumn = [
    // [
    //   {
    //     content: `NO Pindah : ${item._id.no_pindah}`,
    //     colSpan: 9,
    //   },
    // ],
    [
      {
        content: `NO`,
      },
      {
        content: `GUDANG ASAL`,
      },
      {
        content: `BAKI ASAL`,
      },
      {
        content: `GUDANG`,
      },
      {
        content: `BAKI TUJUAN`,
      },
      {
        content: `BARCODE`,
      },
      {
        content: `NAMA BARANG`,
      },
    
      {
        content: `NAMA ATR`,
      },
      {
        content: `BRT ATR`,
        styles: {
          halign: 'right'
        },
      },
      {
        content: `BERAT`,
        styles: {
          halign: 'right'
        },
      },
    ],
  ];
  let i = 1;
  data.forEach((item, index) => {
    let total_berat_atribut = 0;
    let total_berat = 0;
    // let data = [
    //   {
    //     content: `NO PINDAH : ${item._id.no_pindah}`,
    //     colSpan: "9",
    //   },
    // ];
    // tableRows.push(data);
    
    item.detail.forEach((barang, index) => {
      let rows = [
        i,
        barang.kode_gudang_asal,
        barang.kode_baki_asal,
        barang.kode_gudang,
        barang.kode_baki,
        barang.kode_barcode,
        barang.nama_barang,
        barang.nama_atribut,
        {
          content: formatGram(barang.berat_atribut, 3),
          styles: {
            halign: 'right'
          },
        },
        {
          content: formatGram(barang.berat, 3),
          styles: {
            halign: 'right'
          },
        }
      ];
      total_berat = total_berat + parseFloat(barang.berat);
      total_berat_atribut = total_berat_atribut + parseFloat(barang.berat_atribut);
      
      grantotalberat = grantotalberat + parseFloat(barang.berat);
      grantotalberatatr = grantotalberatatr + parseFloat(barang.berat_atribut);
      tableRows.push(rows);
      ++i
    })
    total_barang = total_barang+item.detail.length;
    // localStorage.setItem('hasil',JSON.stringify(item.detail.length))
    // console.log();
    // let total = [
    //   {
    //     content: `Total`,
    //     colSpan: 6,
    //     styles: {
    //       halign: 'center',
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${total_berat.toFixed(3)}`,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: ``,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },
    //   {
    //     content: `${total_berat_atribut.toFixed(3)}`,
    //     styles: {
    //       fillColor: "#E8E5E5",
    //       textColor: "#000",
    //     },
    //   },

    //   // `${sub_qty}`,
    // ];
    // tableRows.push(total);
  });
  let footer = [
    {
      content: `Total`,
      colSpan: 7,
      styles: {
        halign: 'center',
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
  
    {
      content: ``,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
      },
    },
    {
      content: `${formatGram(grantotalberatatr, 3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'

      },
    },
    {
      content: `${formatGram(grantotalberat, 3)}`,
      styles: {
        fillColor: "#E8E5E5",
        textColor: "#000",
        halign: 'right'
      },
    },

    // `${sub_qty}`,
    // {
    //   content: `Total Barang : ${total_barang} Pcs \nTotal Berat : ${grantotalberat.toFixed(
    //     3
    //   )}\nTotal Berat Atribut : ${grantotalberatatr.toFixed(3)}`,
    //   colSpan: 10,
    //   styles: {
    //     fillColor: "#E8E5E5",
    //     textColor: "#000",
    //   },
    // },
  ];

  tableRows.push(footer);
  let printed = [
    {
      colSpan:9,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
    // let footer = [
    //   {
    //     content: `total : `,
    //     colSpan: 6,
    //   },
    //   `${sub_total.toFixed(3)}`,
    //   "",
    //   `${berat_atribut.toFixed(1)}`,
    // ];
    // footRows.push(footer);
    doc.autoTable({
      head: tableColumn,
      body: tableRows,
      foot: footRows,
      startY: finalY,
      theme: "plain",
      rowPageBreak: "avoid",
      // pageBreak: "avoid",
      margin: { top: 10 },
      footStyles: {
        // lineWidth: 0.02, lineColor: "#000",
        fontSize:8,
      },
      bodyStyles: {
        // lineWidth: 0.02, lineColor: "#000",
        fontSize:8,
      },
      headStyles: {
        fontSize:8,
        // lineWidth: 0.02,
        // lineColor: "#000",
        textColor: "#000",
        fillColor: "#E8E5E5",
      },
    });
    finalY = doc.autoTableEndPosY() + 3;
    tableRows = [];
    footRows = [];
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(`User                        : ${JSON.parse(localStoragedecryp("userdata")).user_id}`, 14, finalY + 10);
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  // doc.text(`Grand Total Berat   : ${parseFloat(grantotalberat).toFixed(3)}`, 14, finalY + 22);
  // doc.text(`Grand Total Barang: ${parseFloat(total_barang)}`, 14, finalY + 28);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  doc.save(`Laporan Pindah Barang.pdf`);
  // var string = doc.output("datauristring");
  // // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  // var x = window.open();
  // x.document.open();
  // x.document.write("<html><head><title>Laporan Pindah Barang</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" + 
  // string + "></embed></body></html>");
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakLaporanPindahBarang;


