import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { localStoragedecryp,formatGram, getUserdata } from "../../../../../components/helpers/function";
class LaporanPindahBarangExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let stock_on_hand = 0;
    let berat = 0;
    let berat_atribut = 0;
    let total_barang = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail.forEach((item, index) => {
        stock_on_hand = stock_on_hand + item.stock_on_hand;
        berat_atribut = berat_atribut + item.berat_atribut;
        berat = berat + item.berat;
      });
      total_barang = total_barang + barang.detail.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Pindah Barang"
          sheet="Laporan Pindah Barang"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="10"> Laporan Pindah Barang </th>
            </tr>
            <tr>
              <th colSpan="10"> Periode </th>
            </tr>
            <tr>
              <th colSpan="10">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="10"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="10"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="10"></th>
            </tr>
            <tr>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>NO</th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                GUDANG ASAL
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BAKI ASAl
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                GUDANG
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BAKI TUJUAN
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BARCODE
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA BARANG
              </th>
             
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                NAMA ATRIBUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT ATRIBUT
              </th>
              <th style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                BERAT
              </th>
            </tr>
          </thead>
          <tbody>
            {this.props.data.map((item, no) => (
              <>
                {/* <tr>
                  <th style={{ textAlign: "left" }}> No Pindah </th>
                  <th colSpan="9" style={{ textAlign: "left" }}>
                    {" "}
                    {item._id.no_pindah}{" "}
                  </th>
                </tr> */}

                {item.detail.map((row, index) => (
                  <tr key={row.no_pindah}>
                    <td style={{textAlign : "center"}}> {++no}</td>
                    <td> {row.kode_gudang_asal}</td>
                    <td> {row.kode_baki_asal}</td>
                    <td style={{textAlign : "center"}}> {row.kode_gudang}</td>
                    <td style={{textAlign : "center"}}> {row.kode_baki}</td>
                    <td>&nbsp;{row.kode_barcode}</td>
                    <td> {row.nama_barang}</td>
                    <td> {row.nama_atribut}</td>
                    <td> {formatGram(row.berat_atribut,2)}</td>
                    <td> {formatGram(row.berat,3)}</td>
                  </tr>
                ))}
                {/* <tr>
                <td colSpan="6" style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"center" }}>
                  {" "}
                  Total
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.berat)
                    .reduce((a, b) => a + b, 0)}{" "}
                </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </td>
                <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                  {" "}
                  {item.detail
                    .map((list) => list.berat_atribut)
                    .reduce((a, b) => a + b, 0)}{" "}
                </td>
              </tr> */}
              </>
            ))}
          </tbody>
          <tfoot>
            <tr>
              <td
                colSpan="7"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "center",
                }}
              >
                Total
              </td>
             
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}> </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {formatGram(berat_atribut,2)}
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                {formatGram(berat,3)}
              </td>
              {/* <th
              colSpan="9"
              style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign:"left" }}
            >
              Total Brang : {total_barang} Pcs<br />
              Total Berat : {berat.toFixed(3)} <br />
              Total Berat Atribut : {berat_atribut.toFixed(3)}
            </th> */}
            </tr>
          </tfoot>
        </table>
      </>
    );
  }
}
export default LaporanPindahBarangExel;
